<template>
  <div>


    
    <div v-show="$route.path == '/index/home'" class="titlt">
      <img :src="logo" alt />
      <van-search class="input" placeholder="搜索" />
    </div>
    <transition enter-active-class="animate__animated animate__bounceInLeft">
      <router-view class="main"></router-view>
    </transition>
    <!-- <footer class="footer">
      <router-link v-for="item in navs" :key="item.path" :to="item.path" active-class="select">
         <img :src="$route.path===item.path?item.select_img:item.noSelect" alt="">
          <div>{{item.text}}</div>
      </router-link>
    </footer>-->

    <van-tabbar v-model="active" class="footer">
      <van-tabbar-item v-for="item in navs" :key="item.path" :to="item.path">
        <span>{{item.text}}</span>
        <template #icon>
          <img :src="$route.path===item.path ? item.select_img : item.noSelect" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import tab_home_hig from "../../assets/img/tab_home_hig.png";
import tab_home_nor from "../../assets/img/tab_home_nor.png";
import tab_me_hig from "../../assets/img/tab_me_hig.png";
import tab_me_nor from "../../assets/img/tab_me_nor.png";
import tab_menu_hig from "../../assets/img/tab_menu_hig.png";
import tab_menu_nor from "../../assets/img/tab_menu_nor.png";
import tab_shopping_hig from "../../assets/img/tab_shopping_hig.png";
import tab_shopping_nor from "../../assets/img/tab_shopping_nor.png";
import logo from "../../assets/img/img/home/logo.jpg";
export default {
  data() {
    return {
      active: 0,
      icon: {
        active: "https://img.yzcdn.cn/vant/user-active.png",
        inactive: "https://img.yzcdn.cn/vant/user-inactive.png"
      },
      navs: [
        {
          path: "/index/home",
          text: "首页",
          select_img: tab_home_hig,
          noSelect: tab_home_nor
        },
        {
          path: "/index/cate",
          text: "分类",
          select_img: tab_menu_hig,
          noSelect: tab_menu_nor
        },
        {
          path: "/index/shop",
          text: "购物车",
          select_img: tab_shopping_hig,
          noSelect: tab_shopping_nor
        },
        {
          path: "/index/mine",
          text: "我的",
          select_img: tab_me_hig,
          noSelect: tab_me_nor
        }
      ],
      logo
    };
  }
};
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';

.main {
  padding-bottom: $footer;
}

.footer {
  width: 100vw;
  height: 0.8rem;
  background: #fff;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
}

.footer a {
  flex: 1;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.3rem;
  color: #000;
}

.footer .select {
  background: $primary;
  color: #fff;
}

.footer2 {
  width: 100vw;
  height: 1rem;
  position: fixed;
  left: 0;
  bottom: 0;
  border-top: 0.01rem solid #999;
  display: flex;
  background: #fff;
}

.footer2 a {
  flex: 1;
}

.footer2 a img {
  width: 0.4rem;
  height: 0.4rem;
  display: block;
  margin: 0.1rem auto;
}

.footer2 a div {
  text-align: center;
  line-height: 0.4rem;
  color: #333;
}

.footer2 .select div {
  color: $primary;
}

.titlt {
  width: 100vw;
  background: #ffffff;
  height: $h5;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;

  img {
    margin-top: 0.1rem;
    margin-left: 0.5rem;
  }

  .input {
    width: 2rem;
    height: 0.3rem;
    float: right;
    padding: 0;
    margin-top: 0.25rem;
    margin-right: 1rem;
  }
}
</style>


